.question{
    padding: 100px 0;
    .media-block({
        padding: 50px 0;
    }, 991px);
    .media-block({  
        padding: 20px 0;
    },576px);
    &__title{
        font-weight: bold;
        font-size: 60px;
        color: #407BFF;
        margin-bottom: 30px;
        .media-block({
            margin-bottom: 0px;
            font-size: 40px;
        }, 768px);
        .media-block({  
            font-size: 50px;
        },1200px);
        .media-block({  
            font-size: 30px;
            margin-bottom: 0px;
        },576px);
    }
    &__row{
        display: flex;
        flex-wrap: wrap;
    }
    &__block{
        display: flex;
        .media-block({
            flex-wrap: wrap;
        }, 1600px);
        &-right{
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
    }
    &__images{
        &-img{
            max-width: 100%;
        }
    }
    &__box{
        margin-bottom: 30px;
        margin-right: 30px;
        &.active{
            .question__box-label::before{
                content: "-";
            }
            .question__box-content{
                height: 150px;
                padding: 15px 20px;
            }
        }
        &-label{
            display: flex;
            justify-content: space-between;
            padding: 15px 20px;
            background: #FF5940;
            color: white;
            font-weight: bold;
            font-size: 18px;
            .media-block({  
                font-size: 16px;
            },576px);
            cursor: pointer;
            position: relative;
            box-shadow: 0 0 15px 6px rgb(158, 157, 157);
            &::before{
                content: "+";
                position: absolute;
                top: 50%;
                right: 20px;
                transform: translateY(-50%);
                font-size: 30px;
            }
        }
        &-content{
            line-height: 27px;
            font-size: 18px;
            font-weight: 400;
            background: rgb(228, 227, 227);
            height: 0;
            overflow: hidden;
            transition: 500ms;
        }
    }
}
