.IndexPortfolio{
    padding: 100px 0;
    .media-block({
        padding: 50px 0;
    }, 991px);
    &__top{
        margin-bottom: 90px;
        .media-block({
            margin-bottom: 40px;
        }, 991px);
        .media-block({
            margin-bottom: 0px;
        }, 768px);
        display: flex;
        justify-content: space-between;
    }
    &__right{
        &-buttons{
            .media-block({
                display: none;
            }, 400px);
            .fa-arrow-left{
                animation: fasAnimate 1s linear infinite;
            }
        }
        &-btnOn{
            font-size: 53px;
            color: white;
            background: transparent;
            border: none;
            display: none;
        }
        &-btnOff{
            font-size: 53px;
            color: black;
            background: transparent;
            border: none;
        }
        &-images1{
            position: absolute;
            top: 180px;
            left: 15%;
        }
        &-images2{
            position: absolute;
            top: 400px;
            right: 20px;
        }
    }
    &__title{
        font-weight: bold;
        font-size: 60px;
        color: #4B53E4;
        .media-block({
            margin-bottom: 40px;
            font-size: 40px;
        }, 768px);
        .media-block({  
            font-size: 50px;
        },1200px);
        .media-block({
            font-size: 25px;
        }, 400px);
        .media-block({
            font-size: 30px;
        }, 576px);
    }
    &__block{
        display: flex;
        flex-wrap: wrap;
        .media-block({
            justify-content: center;
            .col-12{
                padding: 0;
            }
        }, 400px);
    }
    &__img{
        display: flex;
        position: relative;
        &-positionBtn{
            position: absolute;
            top: 50%;
            left: 35%;
            background: #4B53E4;
            color: white;
            padding: 10px 30px;
            border: none;
            cursor: pointer;
            border-radius: 50px;
            opacity: 0;
            transition: 500ms;
            &:hover{
                box-shadow: 0px 0px 10px 3px rgb(255, 255, 255);
            }
        }
    }
    &__images{
        width: 100%;
    }
    &__box{
        margin-bottom: 50px;
        cursor: pointer;
        &:hover{
            .IndexPortfolio__img-positionBtn{
                opacity: 1;
            }
        }
    }
    &__item{
        color: white;
        background: #FF5940;
        text-align: center;
        padding: 15px 20px;
        .media-block({
            padding: 10px 15px;
        }, 576px);
       &-title{
        font-size: 30px;
        font-weight: 700;
        margin-bottom: 15px;
        .media-block({
            font-size: 20px;
            font-weight: 400;
            margin-bottom: 10px;
        }, 576px);
       }  
       &-text{
        font-size: 18px;
        line-height: 22px;
        .media-block({
            font-size: 14px;
            line-height: 18px;
        }, 576px);
       }   
    }
    &__button{
        background: #F6A915;
        box-shadow: 0px 0px 10px 3px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        border: none;
        cursor: pointer;
        font-weight: bold;
        font-size: 31px;
        color: white;
        padding: 30px 60px;
        transition: 500ms;
        .media-block({
            padding: 10px 30px;
            font-size: 24px;
        }, 991px);
        &:hover{
            box-shadow: 0px 0px 20px 8px rgba(0, 0, 0, 0.25);
        }
    }
}

@keyframes fasAnimate {
    0%{
        transform: translateX(0);
    }    
    50%{
        transform: translateX(10px);
    }
    100%{
        transform: translateX(0);
    }
}