.header{
    &__nav{
        background: #4B53E4;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        &-close{
            background: transparent;
            color: white;
            font-size: 40px;
            border: none;
            margin: 10px 0 0 20px;
            cursor: pointer;
        }
        &-fixed{
            display: none;
            &.active{
                transform: translateX(0px);
            }
            .media-block({
                 background: black;
                 position: absolute;
                 top: 0;
                 left: 0;
                 width: 50%;
                 height: 500px;
                 display: block;
                 transform: translateX(-600px);
                 transition: 500ms;
                 z-index: 999;
            }, 1000px);
        }
    }
    &__fixed{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        &_list{
            margin: 30px 0;
            .media-block({  
                margin: 20px 0;
            },576px);
        }
        &_link{
            color: white;
            text-decoration: none;
            font-size: 18px;
            font-weight: 700;
            .media-block({  
                font-size: 16px;
                font-weight: 400;
            },576px);
        }
    }
    &__menu_top{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__rigthMenu{
        display: flex;
        align-items: center;
        .media-block({
            display: none;
        }, 1000px);
    }
    &__menu{
        &-gamburger{
            display: none;
            .media-block({
                display: block;
                font-size: 40px;
                border: none;
                background: transparent;
                color: white;
            }, 1000px);
        }
        display: flex;
        &_list{
            margin: 0 30px;
            .media-block({
                margin: 0 20px;
            }, 1200px);
            .media-block({
                margin: 0 10px;
            }, 1092px);
        }
        &_link{
            color: white;
            text-decoration: none;
            font-size: 18px;
            font-weight: 700;
            .media-block({
                font-size: 14px;
            }, 1200px);
            .media-block({
                font-size: 17px;
            }, 1315px);
            .media-block({
                font-size: 14px;
            }, 1284px);
            &:hover{
                color: #F6A915;
            }
        }
        &_tel{
            background: #F6A915;
            padding: 15px 30px;
            border-radius: 100px;
            color: white;
            font-weight: bold;
            font-size: 18px;
            border: none;
            box-shadow: 0px 0px 16px 6px rgba(255, 255, 255, 0.25);
        }
    }
    &__left{
        display: flex;
        flex-wrap: wrap;
        cursor: pointer;
        min-height: 850px;
        .media-block({
            min-height: 300px;
         }, 991px);
        &_owl{
            padding: 20px;
        }
        &_relative{
            position: relative;
        }
        &_img{
            background: url('../img/Header_Bg.png') no-repeat center center / cover;
            min-height: 862px;
            position: absolute;
            width: 100%;
            .media-block({
               background-image: none;
               background: #4B53E4;
               min-height: 500px;
            }, 991px);
        }
        &_block{
            padding-top: 150px !important;
        }
        &_title{
            font-weight: bold;
            font-size: 90px;
            color: white;
            .media-block({
                font-size: 50px;
            }, 1200px);
            .media-block({
                font-size: 22px;
            }, 400px);
            .media-block({
                font-size: 30px;
            }, 576px);
        }
        &_text{
            margin: 20px 0;
            font-weight: bold;
            font-size: 55px;
            color: white;
            background: #FF5940;
            display: inline-block;
            padding: 10px 20px;
            .media-block({
                font-size: 30px;
            }, 1200px);
            .media-block({
                font-size: 16px;
                font-weight: 400;
                padding: 6px 8px;
                margin: 10px 0;
            }, 400px);
            .media-block({
                font-size: 20px;
            }, 576px);
        }
        &_text2{
            font-weight: 400;
            font-size: 22px;
            line-height: 26px;
            color: white;
            margin-bottom: 35px;
            .media-block({
                font-size: 18px;
            }, 1200px);
            .media-block({
                font-size: 16px;
            }, 400px);
        }
        &_btn{
            background: #F6A915;
            font-weight: bold;
            font-size: 22px;
            color: white;
            border-radius: 100px;
            padding: 15px 25px;
            box-shadow: 0px 0px 12px 1px;
            border: none;
            transition: 500ms;
            z-index: 55;
            .media-block({
                padding: 10px 15px;
                font-size: 16px;
                font-weight: 400;
            }, 400px);
            &:hover{
                box-shadow: 0px 0px 16px 6px
            }
            .media-block({
                padding: 10px 20px;
                font-size: 18px;
            }, 1200px);
        }
    }
    &__right{
        position: relative;
        &_img{
            position: absolute;
            top: 137px;
            right: 23%;
            width: 100%;
            background: url('../img/header_right_img.png') no-repeat; 
            min-height: 800px;
            z-index: 5;
        }
    }

}
