.desinger{
    padding-top: 200px;
    .media-block({  
        padding-top: 150px;
        padding-bottom: 60px;
    }, 992px);
    padding-bottom: 100px;
    &__title{
        font-weight: bold;
        font-size: 60px;
        color: #4B53E4;
        text-align: center;
        margin-bottom: 80px;
        .media-block({  
            margin-bottom: 0px;
        }, 991px);
        .media-block({ 
            font-size: 55px; 
        }, 1200px);
        .media-block({  
            font-size: 50px;
            margin-bottom: 40px;
        }, 992px);
        .media-block({
            font-size: 40px;
        }, 768px);
        .media-block({
            font-size: 25px;
        }, 400px);
        .media-block({
            font-size: 30px;
        }, 576px);
    }
    &__row{
        display: flex;
        flex-wrap: wrap;
    }
    &__img{
        max-width: 100%;
    }
    &__box{
        &-title{
            font-weight: bold;
            font-size: 50px;
            color: #FFFFFF;
            background: #FF5940;
            padding: 15px 30px;
            border-radius: 10px;
            .media-block({  
                font-size: 40px;
            }, 1200px);
            .media-block({  
                font-size: 30px;
            }, 992px);
            .media-block({  
                margin-top: 50px;
            }, 991px);
            .media-block({
                margin-top: 30px;
                font-size: 20px;
            }, 576px);
        }
        &-text{
            background: #FFC244;
            box-shadow: 0px 4px 29px 10px rgba(168, 161, 161, 0.25);
            padding: 15px 30px;
            margin-top: 20px;
            &-par{
                font-size: 25px;
                font-weight: 400;
                line-height: 144.8%;
                letter-spacing: 0.06em;
                color: #4B53E4;
                .media-block({  
                    font-size: 20px;
                    line-height: 24px;
                }, 992px);
                .media-block({
                    font-size: 16px;
                    line-height: 24px;
                }, 576px);
            }
            &-btn{
                font-weight: bold;
                display: inline-block;
                font-size: 25px;
                line-height: 144.8%;
                letter-spacing: 0.06em;
                color: #FFFFFF;
                background: #8672E8;
                box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
                border-radius: 100px;
                padding: 15px 30px;
                text-decoration: none;
                margin-top: 30px;
                .media-block({
                    padding: 8px 10px;
                    font-size: 16px;
                    font-weight: 400;
                }, 576px);
                .media-block({  
                    padding: 10px 20px;
                    font-size: 20px;
                    font-weight: 400;
                }, 992px);
                &:hover{
                    color: #4B53E4;
                }
            }
        }
    }
}