.WebSayt{
    padding-top: 200px;
    padding-bottom: 100px;
    &__box{
        &-img{
            .media-block({
                display: none;
            }, 500px);
        }
    }
    .media-block({
        padding-top: 150px;
    }, 991px);
    &__title{
        font-weight: bold;
        font-size: 60px;
        color: #4B53E4;
        text-align: center;
        margin-bottom: 80px;
        .media-block({
            margin-bottom: 40px;
        }, 991px);
        .media-block({
            margin-bottom: 40px;
            font-size: 40px;
        }, 768px);
        .media-block({  
            font-size: 50px;
        },1200px);
        .media-block({  
            font-size: 30px;
        },576px);
    }
    &__row{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    &__img{
        max-width: 100%;
    }
    &__box{
        &-images{
            .media-block({
                display: none;
            }, 500px);
        }
        &-right{
            display: flex;
            .media-block({
                flex-wrap: wrap-reverse;
            }, 1300px);
            margin-bottom: 50px;
            &-top{
                .media-block({
                    margin: 0;
                    margin-bottom: 10px;
                }, 1300px);
            }
            &-block{
                .media-block({
                    margin: 0;
                    margin-bottom: 30px;
                }, 1300px);
                margin-left: 30px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                align-items: flex-start;
            }
            &-title{
                font-weight: bold;
                font-size: 24px;
                color: #4B53E4;
                margin-bottom: 15px;
            }
            &-text{
                font-weight: 400;
                font-size: 18px;
                line-height: 22px;
                color: #4B53E4;
            }
            &-btn{
                font-weight: normal;
                font-size: 16px;
                line-height: 19px;
                color: #FFFFFF;
                background: #8672E8;
                box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
                border-radius: 50px;
                border: none;
                cursor: pointer;
                padding: 5px 10px;
            }
        }
        &-left{
            display: flex;
            margin-bottom: 50px;
         
            .media-block({
                flex-wrap: wrap;
            }, 1300px);
            margin-bottom: 50px;
            &-top{
                .media-block({
                    margin: 0;
                    margin-bottom: 10px;
                }, 1300px);
            }
            &-block{
              
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                align-items: flex-end;
                margin-right: 30px;
                .media-block({
                    margin: 0;
                    margin-bottom: 30px;
                    align-items: flex-start;
                }, 1300px);
            }
            &-title{
                font-weight: bold;
                font-size: 24px;
                color: #4B53E4;
                margin-bottom: 15px;
                text-align: end;
                .media-block({
                    text-align: left;
                }, 1300px);
            }
            &-text{
                font-weight: 400;
                font-size: 18px;
                line-height: 22px;
                color: #4B53E4;
                text-align: end;
                .media-block({
                    text-align: left;
                }, 1300px);
            }
            &-btn{
                font-weight: normal;
                font-size: 16px;
                line-height: 19px;
                color: #FFFFFF;
                background: #8672E8;
                box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
                border-radius: 50px;
                border: none;
                cursor: pointer;
                padding: 5px 10px;
            }
        }
    }
    &__btnPortfolio{
        display: flex;
        justify-content: center;
        &-btn{
            display: block;
            background: #F6A915;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            border-radius: 100px;
            font-weight: bold;
            font-size: 20px;
            color: #FFFFFF;
            padding: 20px 50px;
            text-decoration: none;
            transition: 500ms;
            &:hover{
                box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.75);
                color: #4B53E4;
            }
            .media-block({
                padding: 10px 30px;
                font-size: 18px;
            }, 576px);
        }
    }
}