.aboutUS__block{
    padding-top: 200px;
    .media-block({
        padding-top: 150px;
    }, 768px);
    .media-block({
        padding-top: 150px;
    }, 576px);
    &-title{
        font-weight: bold;
        font-size: 60px;
        color: #4B53E4;
        margin-bottom: 80px;
        text-align: center;
        .media-block({
            font-size: 40px;
            margin-bottom: 40px;
        }, 768px);
        .media-block({  
            font-size: 50px;
        }, 1200px);
        .media-block({
            font-size: 25px;
        }, 400px);
        .media-block({
            font-size: 30px;
            margin-bottom: 20px;
        }, 576px);
    }
    &-row{
        display: flex;
        flex-wrap: wrap;
    }
    &-text{
        font-weight: bold;
        font-size: 22px;
        line-height: 190%;
        color: #FF5940;
        margin-bottom: 30px;
        .media-block({
            font-size: 20px;
        }, 768px);
        .media-block({
            font-size: 16px;
         }, 576px);
        &-span{
            color: white;
            background: #FF5940;
            display: inline-block;
            padding: 5px 10px;
            border-radius: 10px;
        }
    }
    &-link{
        font-weight: bold;
        font-size: 22px;
        line-height: 190%;
        color: #4B53E4;
        .media-block({
            font-size: 20px;
        }, 768px);
        .media-block({
            font-size: 16px;
         }, 576px);
    }
    &-img{
        max-width: 100%;
    }
}

.aboutUS__trust{
    padding-top: 80px;
    padding-bottom: 80px;
    .media-block({
        padding-top: 40px;
        padding-bottom: 40px;
    }, 768px);
    &-title{
        font-weight: bold;
        font-size: 60px;
        color: #4B53E4;
        margin-bottom: 80px;
        text-align: center;
        .media-block({
            font-size: 40px;
            margin-bottom: 40px;
        }, 768px);
        .media-block({
            font-size: 30px;
         }, 576px);
    }
    &-row{
        display: flex;
        flex-wrap: wrap;
    }
    &-box{
        margin-bottom: 50px;
    }
    &-img{
        max-width: 100%;
    }
}