.about{
    margin-top: 200px;
    padding-top: 80px;
    .media-block({
        padding-top: 20px;
    }, 576px);
    .media-block({
        padding-top: 40px;
    }, 768px);
    .media-block({
        margin-top: 200px;
    }, 400px);
  
    background: black;
    &__row{
        display: flex;
        flex-wrap: wrap;
        .media-block({  
            flex-direction: column;
        },1200px);
        margin-bottom: 50px;
    }
    &__title{
        font-weight: bold;
        font-size: 60px;
        color: #4B53E4;
        margin-bottom: 90px;
        .media-block({
            margin-bottom: 40px;
            font-size: 40px;
        }, 768px);
        .media-block({  
            font-size: 50px;
        },1200px);
        .media-block({
            font-size: 25px;
            margin-bottom: 0px;
        }, 400px);
        .media-block({
            font-size: 30px;
            margin-bottom: 0px;
        }, 576px);
    }
    &__block{
        flex-wrap: wrap;
        .media-block({
            flex-wrap: wrap-reverse;
        }, 576px)
    }
    &__img{
        box-shadow: 0px 0px 27px #746E6E;
        display: flex;
        margin-top: 30px;
        .media-block({
            display: none
        },1400px);
    }

    &__images{
        width: 100%;
    }
    &__item{
        .media-block({
            margin-bottom: 40px;
        }, 991px);
        &-icon{
            margin-right: 20px;
            .media-block({
               display: none;
            }, 400px);
        }
        &-icon i{
            font-size: 35px;
            color: #30B47A;
        }
        &-title{
            font-weight: bold;
            font-size: 25px;
            color: white;
            background: #FF5940;
            display: inline-block;
            padding: 5px 10px;
            .media-block({
                font-size: 18px;
                font-weight: 400;
            }, 400px);
            .media-block({
                font-size: 20px;
            }, 576px);
        }
        &-text{
            font-weight: 400;
            font-size: 18px;
            line-height: 22px;
            color: #7F86F6;
            margin-top: 15px;
            margin-right: 20px;
            .media-block({
                color: white;
                font-size: 16px;
            }, 400px);
        }
        &-btn{
            background: #F6A915;
            padding: 10px 20px;
            border: none;
            border-radius: 100px;
            color: white;
            font-weight: bold;
            font-size: 16px;
            margin-top: 20px;
            .media-block({
                font-weight: 400;
            }, 400px);
            &:hover{
                box-shadow: 0px 0px 16px 8px rgba(255, 255, 255, 0.25);
            }
        }
    }

    &__right{
        &-relative{
            position: relative;
        }
        &-buttons{
            position: absolute;
            top: 0;
            right: 0;
            .media-block({
               display: none;
            }, 400px);
            .fa-arrow-left{
                animation: fasAnimate 1s linear infinite;
            }
            .media-block({
                top: -68px;
                right: -55px;
            }, 576px);
        }
        &-btnOn{
            font-size: 53px;
            color: white;
            background: transparent;
            border: none;
            .media-block({
                font-size: 30px;
            }, 576px);
            .media-block({
                font-size: 48px;
            }, 768px);
        }
        &-btnOff{
            font-size: 53px;
            color: black;
            background: transparent;
            border: none;
            display: none;
        }
        &-images1{
            position: absolute;
            top: 180px;
            left: 15%;
            max-width: 100%;
        }
        &-images2{
            position: absolute;
            top: 400px;
            right: 20px;
            max-width: 100%;
        }
    }

}


@keyframes fasAnimate {
    0%{
        transform: translateX(0);
    }    
    50%{
        transform: translateX(10px);
    }
    100%{
        transform: translateX(0);
    }
}