.footer{
    background: url('../img/Footer/Footer_botoom_1.jpg');
    min-height: 869px;
    padding-top: 100px;
    .media-block({
        padding-top: 50px;
        min-height: 600px;
    },768px);
    .media-block({
        min-height: 350px;
        padding-top: 30px;
    },576px);
    
    &__nav{
        display: flex;
        justify-content: center;
    }
    &__centerMenu{
        display: flex;
        align-items: center;

    }
    &__menu{
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        &_list{
            margin: 0 30px;
            .media-block({
                margin: 0 20px;
            },992px);
            .media-block({
                margin: 20px 10px;
            },576px);
        }
        &_link{
            font-weight: bold;
            font-size: 22px;
            color: #FFFFFF;
            text-decoration: none;
            .media-block({
                font-size: 18px;
            },1317px);
            .media-block({
                font-size: 18px;
                font-weight: 400;
            },992px);
            &:hover{
                color: #F9795A;
            }
        }
        &_tel{
            padding: 15px 30px;
            background: #F6A915;
            box-shadow: 0px 0px 16px 6px rgba(255, 255, 255, 0.25);
            border-radius: 100px;
            border: none;
            font-weight: bold;
            font-size: 22px;
            color: white;
            display: inline-block;
            .media-block({
                display: none;
            },992px);
        }
    }
    &__text{
        text-align: center;
        color: white;
        font-weight: bold;
        font-size: 22px;
        margin-top: 50px;
        .media-block({
            margin-top: 20px;
        }, 576px);
        .media-block({
            font-size: 18px;
            font-weight: 400;
        }, 576px);
        &-span{
            text-decoration: underline;
        }
    }
    &__social{
        display: flex;
        justify-content: center;
        margin-top: 50px;
        &-menu{
            display: flex;
            justify-content: center;
        }
        &-item{
            margin: 0 30px;
        }
        &-link{
            color: rgb(253, 216, 6);
            font-size: 40px;
            .media-block({
                font-size: 30px;
            }, 576px);
            &:hover{
                color: black;
            }
        }
    }
}