.aboutUs{
    padding-top: 80px;
    .media-block({
        padding-top: 40px;
    }, 991px);
    &__title{
        font-weight: bold;
        font-size: 60px;
        color: #4B53E4;
        margin-bottom: 60px;
        .media-block({
            margin-bottom: 40px;
        }, 991px);
        .media-block({
            margin-bottom: 40px;
            font-size: 40px;
        }, 768px);
        .media-block({  
            font-size: 50px;
        },1200px);
        .media-block({  
            font-size: 30px;
            margin-bottom: 0;
        },576px);
    }
    &__block{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        .media-block({
            justify-content: center
        }, 991px);
    }
    &__box{
        display: flex;
        margin-bottom: 60px;
        box-shadow: 0px 0px 20px 6px rgba(0, 0, 0, 0.25);
        padding: 10px 20px;
        border-radius: 5px;
        &-title{
            font-weight: bold;
            font-size: 25px;
            background: #FF5940;
            color: white;
            padding: 10px;
            margin-bottom: 20px;
            border-radius: 5px;
            .media-block({  
                font-size: 18px;
                font-weight: 400;
                margin-bottom: 10px;
            },576px);
        }
        &-text{
            font-weight: 400;
            font-size: 18px;
            line-height: 140%;
            color: #407BFF;
            .media-block({  
                font-size: 16px;
            },576px);
        }
        &-icons{
            margin-right: 30px;
            .media-block({  
                margin-right: 15px;
            },576px);
            &-fas{
                font-size: 35px;
                color: #4B53E4;
                .media-block({  
                    font-size: 25px;
                },576px);
            }
        }
    }
    &__images{
        position: relative;
        &-img{
            position: absolute;
            max-width: 100%;
            height: 450px;
            &-1{
                z-index: 2;
            }
            &-2{
                left: 20%;
            }
            &-3{
                right: 15%;
            }
        }
    }
}