.porfolio{
    padding-top: 200px;
    padding-bottom: 100px;
    .media-block({
        padding-top: 150px;
    }, 768px);
    &__title{
        font-weight: bold;
        font-size: 60px;
        color: #4B53E4;
        text-align: center;
        margin-bottom: 80px;
        .media-block({
            margin-bottom: 40px;
        }, 991px);
        .media-block({
            margin-bottom: 40px;
            font-size: 40px;
        }, 768px);
        .media-block({  
            font-size: 50px;
        },1200px);
        .media-block({  
            font-size: 30px;
            margin-bottom: 20px;
        },576px);
    }
    &__container{
        &-title{
            font-weight: bold;
            font-size: 40px;
            line-height: 50px;
            color: #FFFFFF;
            background: #F24B46;
            display: inline-flex;
            padding: 10px 30px 10px 10px;
            border-radius: 10px;
            margin-bottom: 50px;
            .media-block({
                font-size: 30px;
                padding: 10px;
                line-height: 25px;
            }, 768px);
            .media-block({  
                font-size: 20px;
                line-height: 25px;
            },576px);
        }
        
    }
    &__row{
        display: flex;
        flex-wrap: wrap;
    }
    &__box{
        margin-bottom: 50px;
        position: relative;
    }
    &__img{
        width: 100%;
      
            min-height: 250px;
     
    }
    &__absolute{
        position: absolute;
        width: 100%;
        background: #4B53E4;
        bottom: 0;
        left: 0;
        padding: 10px 0 10px;
        text-align: center;
        transition: 500ms;
        cursor: pointer;
        .media-block({
            padding: 4px;
        }, 768px);
        &:hover{
            background: #F24B46;
        }
        &-title{
            font-weight: bold;
            font-size: 20px;
            line-height: 25px;
            color: #FFFFFF;
            .media-block({
                font-size: 18px;
            }, 768px);
            .media-block({  
                font-size: 20px;
                line-height: 25px;
            },576px);
        }
        &-text{
            font-weight: 400;
            font-size: 18px;
            color: #FFFFFF;
            margin-top: 10px;
            .media-block({  
                font-size: 16px;
            },576px);
        }
        &-link{
            background: #FFFFFF;
            border-radius: 50px;
            font-weight: normal;
            font-size: 16px;
            line-height: 19px;
            color: #000000;
            padding: 10px 20px;
            display: inline-block;
            text-decoration: none;
            margin-top: 10px;
            transition: 500ms;
            .media-block({
                padding: 2px 10px;
            }, 768px);
            &:hover{
                color: #F24B46;
                box-shadow: 0 0 10px black;
            }
        }
    }
}