.contact{
    padding-top: 100px;
    padding-bottom: 80px;
    .media-block({
        padding: 50px 0;
    }, 991px);
    .media-block({  
        padding-top: 0px;
    },576px);
    &__title{
        font-weight: bold;
        font-size: 60px;
        color: #407BFF;
        margin-bottom: 80px;
        .media-block({
            margin-bottom: 40px;
        }, 991px);
        .media-block({
            margin-bottom: 0px;
            font-size: 40px;
        }, 768px);
        .media-block({  
            font-size: 50px;
        },1200px);
        .media-block({  
            font-size: 30px;
            margin-bottom: 0px;
        },576px);
    }
    &__top{
        display: flex;
        flex-wrap: wrap;
    }
    &__bottom{
        display: flex;
        flex-wrap: wrap;
        &-img{
            max-width: 100%;
        }
        &-title{
            font-weight: bold;
            font-size: 60px;
            color: #407BFF;
            margin-bottom: 80px;
            .media-block({  
                font-size: 30px;
            },576px);
            .media-block({
                margin-bottom: 40px;
            }, 991px);
            .media-block({
                margin-bottom: 40px;
                font-size: 40px;
            }, 768px);
            .media-block({  
                font-size: 50px;
            },1200px);
        }
    }
    &__box{
        background: #FF5940;
        margin-bottom: 50px;
        color: white;
        display: flex;
        justify-content: space-between;
        padding: 20px 10px;
        font-weight: bold;
        font-size: 25px;
        border-radius: 5px;
        box-shadow: 0 0 15px 6px #9e9d9d;
        .media-block({  
            font-size: 18px;
            padding: 15px 10px;
        },576px);
        &-adress{
            text-decoration: underline;
        }
    }
    &__images{
        height: 600px;
        display: flex;
        justify-content: center;
        &-img{
            max-width: 100%;
            height: auto;
        }
    }
}